import constant from './constants';
import client from './client';

const sendactivity = (senddata) => {
	debugger;
	client.create({
		index: constant.index,
		type: constant.type,
  		body: senddata
	}, (error, response) => {
		console.log(response);
	});
};

export {
	sendactivity
};


